/* Global Reset and Body */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  overflow: hidden;
  background: linear-gradient(135deg, #243B55, #141E30); /* Premium dark gradient */
  color: #fff;
}

/* Main container style */
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  position: relative;
  width: 100%;
  background-size: cover;
  transition: all 0.3s ease-in-out;
}

/* Search box and search bar */
.search-box {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  width: 90%; /* Full width with 10% margin */
  max-width: 700px; /* Max width to maintain some control */
  padding: 0 20px; /* Padding to avoid text sticking to edges */
  margin: 0 auto;
}

.searchbar {
  width: 100%; /* Full width */
  padding: 18px;
  border: 2px solid #fff;
  border-radius: 50px; /* Smoother rounded edges */
  background-color: rgba(255, 255, 255, 0.3); /* Slightly transparent background */
  /* color: #fff; */
  font-size: 18px;
  outline: none;
  text-align: center;
  transition: all 0.3s ease-in-out; /* Smooth transition on focus */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Light shadow for input */
}

.searchbar:focus {
  background-color: rgba(255, 255, 255, 0.4); /* Slightly darker when focused */
  border-color: #ff8c00; /* Warm orange highlight */
  box-shadow: 0 0 15px rgba(255, 140, 0, 0.8); /* Focus glow effect */
}

/* Location box */
.location-box {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 24px;
  z-index: 10;
  padding: 30px;
  border-radius: 15px;
  background-color: rgba(0, 0, 0, 0.6); /* Darker background */
  width: 80%;
  max-width: 500px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
}

.location {
  font-weight: bold;
  color: #ffcc00; /* Golden color */
  font-size: 36px;
  letter-spacing: 1px;
}

/* Date section */
.date {
  font-size: 18px;
  margin-top: 10px;
  font-weight: lighter;
  color: #ddd; /* Soft color for date */
}

/* Weather box */
.weather-box {
  margin-top: 25px;
}

.temp {
  font-size: 60px;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 20px 30px;
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.5);
  color: #ff8c00; /* Warm color for temperature */
}

.weather {
  font-size: 22px;
  margin-top: 10px;
  font-weight: 500;
  text-transform: capitalize;
  color: #fff;
}

/* Add some glow effect for the weather status */
.weather.glowing {
  text-shadow: 0 0 15px rgba(255, 140, 0, 0.7); /* Golden glow effect */
}

/* Adjustments for mobile */
@media (max-width: 600px) {
  .location-box {
    width: 90%;
    padding: 20px;
  }

  .search-box {
    top: 15%;
    width: 90%; /* Ensure the search bar stretches full width on smaller screens */
  }

  .temp {
    font-size: 48px;
  }

  .weather {
    font-size: 18px;
  }

  .weather-box {
    margin-top: 20px;
  }
}
